h1.h1-type {
    width: 99%;
    position: relative;
    top: 48%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-transform: capitalize;
    font-family: Poppins;
    letter-spacing: 5px;
    margin: 0;
    color: transparent;
    font-size: 25px;
    font-weight: 500;
    padding: 9px 0;
    border: 2px solid #fff;
}

h1.h1-type1 {
    position: absolute;
    top: 27%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-transform: uppercase;
    font-family: Poppins;
    letter-spacing: 5px;
    margin: 0;
    padding: 0;
    color: #fff;
    font-size: 22px;
    white-space: nowrap;
}

h1.h1-type:before {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #000;
    overflow: hidden;
    border-right: 1px solid rgba(189, 142, 186, 50%);
    -webkit-animation: type 4s steps(45) infinite;
            animation: type 4s steps(45) infinite;
    white-space: nowrap;
    padding: 9px 8px;
    border: 2px solid #fff;
    background: #fff;
}
h1.h1-type1:before {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #000;
    overflow: hidden;
    border-right: 1px solid #000;
    -webkit-animation: type 5s steps(40) infinite;
            animation: type 5s steps(40) infinite;
    white-space: nowrap;
}
@-webkit-keyframes type {
    0% {
        width: 0;
    }
    25% {
        width: 25%;
    }

    50% {
        width: 70%;
    }
    90% {
        width: 99%;
    }

    100% {
        width: 5%;
    }
}
@keyframes type {
    0% {
        width: 0;
    }
    25% {
        width: 25%;
    }

    50% {
        width: 70%;
    }
    90% {
        width: 99%;
    }

    100% {
        width: 5%;
    }
}
